import { UsersAPI } from "../components/api";
import {stopSubmit} from 'redux-form'

const SET_USER_DATA = 'authReducer/SET_USER_DATA ';

let initialState = {
  id: null,
  email: null,
  login: null,
  isAuth: false,
},
  authReducer = (state = initialState, action) => {
    switch (action.type) {

      case SET_USER_DATA:
        return {
          ...state,
          ...action.payload,
          
        }
      default: return state;
    }
  }

export const setAuthUserData = (id, email, login,isAuth) => ({ type: SET_USER_DATA, payload: { id, email, login,isAuth } })

export const setAuth=()=>async (dispatch)=>{
let response= await  UsersAPI.getAuth()
 

    if (response.data.resultCode === 0) {
      let { id, email, login } = response.data.data
      dispatch(setAuthUserData(id, email, login ,true))
    }

}
export const login=(email,password,rememberMe)=>async (dispatch)=>{
 let response=await  UsersAPI.login(email,password,rememberMe)
 

    if (response.data.resultCode === 0) {
      dispatch(setAuth())
    }else {
      let message=response.data.messages.length>0 ? response.data.messages[0] :'Some Error';
      
      dispatch(stopSubmit('login',{_error:message}))
    }

}
export const logout=()=>async (dispatch)=>{
 let response=await UsersAPI.logout()
  

    if (response.data.resultCode === 0) {
      dispatch(setAuthUserData(null,null,null,false))
    }

}

export default authReducer;



