import * as axios from "axios"

const instance = axios.create(
  {
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {
      "API-key": "78e76ec9-f734-415f-a64d-335bb16d92d5"
    }
  }
)

export const UsersAPI = {
  getUsers: (currentPage = 1, count = 5) => {
    return instance
      .get(`users?page=${currentPage}&count=${count}`)
      .then(response => {
        return response.data
      })
  },
  follower: (id) => {
    return instance
      .post(`follow/${id}`, {},)

  },
  unfollower: (id) => {
    return instance
      .delete(`follow/${id}`)

  },
  getProfile: (userId) => {
    return instance
      .get(`profile/` + userId)
     
  },
  
  getStatus: (userId) => {
    return instance
      .get(`profile/status/` + userId)
      
  },
  updateStatus: (status) => {
    return instance
      .put(`profile/status`, { status: status })
      
  },
  // login: () => {
  //   return instance
  //     .put(`/auth/login`, {  })
      
  // },
  getAuth: () => {
    return instance
      .get(`auth/me`)

  },
  login: (email,password,rememberMe=false) => {
    return instance
      .post(`auth/login`,{email,password,rememberMe})

  },
  logout: () => {
    return instance
      .delete(`auth/login`)

  },

}










